@import '../../assets/sass/variables/_devVariables';
@import '../../assets/sass/_main';

:local(.link) {
  text-decoration: underline;
  margin-left: 10px;

  &:hover {
    cursor: pointer;
  }
}

:local(.closeMap) {
  transition: 0.3s all ease-in-out;
  position: absolute;
  background: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  z-index: 2;
  overflow: hidden;
  margin-top: 20px;
  right: 0;
  border-right-color: white;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  width: 76px;
  height: 76px;

  &:hover {
    background: #e5e5e5;
    width: 180px;
  }
}

:local(.closeButtonLabel) {
  white-space: nowrap;
  padding-top: 25px;
  padding-left: 80px;
  display: inline-block;
}

:local(.mapContainer) {
  background: transparent;
  position: fixed;
  margin-top: 62px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

:local(.container) {
  transition: 0.3s all ease-in-out;
  position: absolute;
  background: #fff;
  border: 2px solid #e5e5e5;
  border-radius: 4px;
  z-index: 2;
  overflow: hidden;
  margin-top: 10px;
  padding-top: 65px;
  border-left-color: white;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;

  > div {
    clear: both;
  }

  &:local(.open) {
    overflow: visible;
    overflow-x: hidden;
    overflow-y: auto;
    width: 350px;
    height: 100%;

    > div {
      margin: 15px 5px;
    }
  }

  &:local(.closed) {
    width: 76px;
    height: 76px;

    &:hover {
      background: #e5e5e5;
    }
    > .nav {
      display: none;
    }
  }
}

:local(.toggleBtn) {
  font-size: 2.5em;
  margin: 20px;
  position: absolute;
  left: 0;
  top: 0;
  color: $color-toggle-on;

  &:hover {
    cursor: pointer;
  }
}
:local(.tabs) { 
  > a {
    width: 50%;
    text-align: center;
    font-weight: 600;
    border: none;    
  }
  &.closed {
    display: none;
  }
}
:local(.search) {
  border: none;
  &.closed {
    display: none;
  }
}
